import React from 'react';
import { useAppSelector } from '../redux';
import { setSidePanelMode } from '../redux/app/action';
import { useDispatch } from 'react-redux';
import { Alert, AlertIcon, Box, CloseButton, Flex, HStack } from '@chakra-ui/react';
import { SidePanelMode } from '../constants/constants';
import RmgDebouncedInput from './common/rmg-debounced-input';
import { updateStationName } from '../redux/param/action';
import RmgLabel from './common/rmg-label';

export default function SidePanel() {
    const dispatch = useDispatch();

    const { sidePanelMode, selectedStation } = useAppSelector(state => state.app);
    const { name } = useAppSelector(state => state.param.stn_list[selectedStation]);

    const handleClose = () => {
        dispatch(setSidePanelMode(SidePanelMode.CLOSE));
    };

    return (
        <Flex
            as="section"
            height="100%"
            maxW={sidePanelMode !== SidePanelMode.CLOSE ? 320 : 0}
            visibility={sidePanelMode !== SidePanelMode.CLOSE ? 'initial' : 'hidden'}
            position="relative"
            boxShadow="xl"
            shrink={0}
            direction="column"
            transition="0.3s"
        >
            <Box width={320}>
                <Alert status="info" variant="solid" size="xs" pl={3} pr={1} pb={0} pt={0}>
                    <AlertIcon />
                    {name[0] + ' - ' + name[1]}
                    <CloseButton ml="auto" onClick={handleClose} />
                </Alert>

                <HStack>
                    <RmgLabel label="Chinese name">
                        <RmgDebouncedInput
                            placeholder=""
                            defaultValue={name[0]}
                            onDebouncedChange={value => dispatch(updateStationName(selectedStation, [value, name[1]]))}
                        />
                    </RmgLabel>

                    <RmgLabel label="English name">
                        <RmgDebouncedInput
                            placeholder="Metro Station"
                            defaultValue={name[1]}
                            onDebouncedChange={value => dispatch(updateStationName(selectedStation, [name[0], value]))}
                        />
                    </RmgLabel>
                </HStack>
            </Box>
        </Flex>
    );
}
